
import { mapMutations } from 'vuex'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
    name: 'about',

    components: {
        Qrcode,
        Blur,
        Group,
        Cell,
    },

    data() {
        return {
            headImg: require("../assets/headpart.jpg"),
            QrImg: require("../assets/qrcode.jpg"),
        }
    },

    mounted() {
        this.SET_TITLE(this.$t('About'))
        this.SET_RIGHT_OPTION({ show: false })
    },

    methods: {
        ...mapMutations(['SET_TITLE', 'SET_RIGHT_OPTION'])
    }
}



